import type { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from '@reduxjs/toolkit';
import { RootState } from './Index';

type appState = {
    code: string | null | undefined,
    //useLazyTSubListAllQuery: useLazyTSubListAllQueryType,
    AccessToken: string | null,
    RefreshToken: string | null,
    isAuthenticated: boolean,
    loading: boolean,
}

//const appStateLocalStorage = localStorage.getItem("appState");

const appStateEmpty: appState = {
    code: null,
    //useLazyTSubListAllQuery: useLazyTSubListAllQuery,
    RefreshToken: null,
    AccessToken: null,
    isAuthenticated: false,
    loading: false
};

const employeeSlice = createSlice({
    name: 'appSlice',
    initialState: (/*appStateLocalStorage ?JSON.parse(appStateLocalStorage) :*/
        appStateEmpty) as appState,

    reducers: {
        employeeCodeSet: (state, { payload: value }: PayloadAction<string | null | undefined>) => {
            return { ...state, code: value };
        },
        //authLoggedOut: (state) => {
        //    localStorage.removeItem("AuthState")
        //    //state = {...authStateEmpty } ;
        //    //state.accessToken = null;
        //    //state.refreshToken = null;
        //    //state.isAuthenticated = false;
        //    return {...authStateEmpty};
        //},
        //setCredentials: (state,{ payload: { user, token } }: PayloadAction<{ user: User; token: string }>) => {
        //    state.user = user 
        //},
    },
})

export default employeeSlice.reducer
export const { employeeCodeSet } = employeeSlice.actions
export const selectEmployeeCode = (state: RootState) => state.employeeSlice.code
