import { createSlice } from '@reduxjs/toolkit'
import type { PayloadAction } from '@reduxjs/toolkit'
import { RootState } from './Index'
import jwtDecode from "jwt-decode";

import { AuthData } from './VerpApi/Auth/Model';
import { AccessUser } from './VerpApi/Access/Model';


type AuthState = {
    AccessUser: AccessUser | null,
    Name: string,
    AccessToken: string | null,
    RefreshToken: string | null,
    isAuthenticated: boolean,
    roles:string[],
    loading: boolean,
}

export interface User {
    first_name: string
    last_name: string
}

export interface Tp {
    email: string,
    "http://schemas.microsoft.com/ws/2008/06/identity/claims/role" : string[],
}

const authStatelocalStorage = localStorage.getItem("AuthState");


const authStateEmpty: AuthState ={
    AccessUser: null,
    RefreshToken: null,
    AccessToken: null,
    Name: '',
    roles:[],
    isAuthenticated: false,
    loading: false
};

function getProperty<T, K extends keyof T>(obj: T, key: K) {
    return obj[key]; // Inferred type is T[K]
}

const authSlice = createSlice({
    name: 'authSlice',
    initialState: (authStatelocalStorage ?
        JSON.parse(authStatelocalStorage) :
        authStateEmpty) as AuthState,

    reducers: {
        authDataParce: (state, { payload: authData }: PayloadAction<AuthData>) => {

            let s: AuthState = {
                ...authStateEmpty,
                AccessToken: authData.AccessToken,
                RefreshToken: authData.RefreshToken,
                //AccessUser: authData.AccessUser,
                Name: authData.Name,
                isAuthenticated: true,
            }

            const decoded = jwtDecode<Tp>(authData.AccessToken); // Returns with the JwtPayload type
            //let y = decoded['http'];
            //var role = decoded["schemas.xmlsoap.org/ws/2008/06/identity/claims/role"];
            //let foo = getProperty(decoded, "http://schemas.microsoft.com/ws/2008/06/identity/claims/role"); // number
            let r = decoded["http://schemas.microsoft.com/ws/2008/06/identity/claims/role"]
            if (Array.isArray(r))
                s.roles = r;
            else if (r !== undefined)
                s.roles = [r as string];
            //console.log(r);
            //console.log(state.AccessToken);
            //console.log(state.isAuthenticated);
            localStorage.setItem("AuthState", JSON.stringify(s));
            return { ...s };
        },
        authLoggedOut: (state) => {
            localStorage.removeItem("AuthState")
            //state = {...authStateEmpty } ;
            //state.accessToken = null;
            //state.refreshToken = null;
            //state.isAuthenticated = false;
            return {...authStateEmpty};
        },
        //setCredentials: (state,{ payload: { user, token } }: PayloadAction<{ user: User; token: string }>) => {
        //    state.user = user 
        //},
    },
})

export default authSlice.reducer

export const { authDataParce, authLoggedOut } = authSlice.actions
export const selectCurrentUser = (state: RootState) => state.authSlice.AccessUser
