import type { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from '@reduxjs/toolkit';
import { projIds } from '../config/appStateConfig';
import { RootState } from './Index';


type appState = {
    projId: string | null,
    //useLazyTSubListAllQuery: useLazyTSubListAllQueryType,

    AccessToken: string | null,
    RefreshToken: string | null,
    isAuthenticated: boolean,
    loading: boolean,
}

const appStateLocalStorage = localStorage.getItem("appState");


const appStateEmpty: appState = {
    projId: projIds.mos,
    //useLazyTSubListAllQuery: useLazyTSubListAllQuery,
    RefreshToken: null,
    AccessToken: null,
    isAuthenticated: false,
    loading: false
};


const appSlice = createSlice({
    name: 'appSlice',
    initialState: (appStateLocalStorage ?
        JSON.parse(appStateLocalStorage) :
        appStateEmpty) as appState,

    reducers: {
        projIdSet: (state, { payload: value }: PayloadAction<string>) => {
            return { ...state, projId: value };
        },
    },
})

export default appSlice.reducer

export const { projIdSet } = appSlice.actions
export const selectProjName = (state: RootState) => state.appSlice.projId
