interface projSetting {
    label: string,
    id: string,
    defPath: string,
    //useTSubUpdateMutation: useTSubUpdateMutationType,
    //useTSubCreateMutation: useTSubCreateMutationType,
    //useLazyTSubListAllQuery: useLazyTSubListAllQueryType,
    //useTSubSetArcFlagMutation: useTSubSetArcFlagMutationType,
}

export enum projIds {
    mos = "mos",
    mo = "mo",
};

export const projSettings = new Map<string, projSetting>([
    [projIds.mos, {
        id: projIds.mos,
        defPath: '/proj/mos/TEvent/List',
        label: 'Москва',
        //useLazyTSubListAllQuery: useLazyTSubListAllQuery,
        //useTSubUpdateMutation: useTSubUpdateMutation,
        //useTSubCreateMutation: useTSubCreateMutation,
        //useTSubSetArcFlagMutation: useTSubSetArcFlagMutation
    }]
])

export function projSettingGet(id:string|null): projSetting {
    if (id) {
        let s = projSettings.get(id);
        if (s)
            return s
    }
    return projSettings.get(projIds.mos)!;
}

